import React, { Component } from 'react'
import { Helmet } from 'react-helmet'
import Order from 'Shared/components/Order'
import withTranslations from 'Shared/hoc/withTranslations'
import { getLocationCode, getPriceWithCurrency } from 'Shared/helpers/payment'
import { Trans } from 'react-i18next'
import './EmployerBuyCredits.scss'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import { isMobile } from 'Shared/helpers/mobile'
import { StaticRoutes } from 'Shared/constants/Routes'
import NotificationService from 'Shared/services/NotificationService'
import { compose } from 'redux'
import { cvSearchPricing } from 'Shared/constants/MetaTags'
import EmployersService from 'Shared/services/EmployersService'
import { ifElse, equals, not, pipe } from 'ramda'

const employersService = new EmployersService()

class EmployerOrderCredits extends Component {
  componentDidMount () {
    this.props.fetchCurrentLocationIfNeeded()
    this.props.isAuthenticated && employersService.checkoutStartedEvent({
      quantity: 50,
      source: 'self-checkout',
      product: 'credits',
      locationCode: getLocationCode()
    })
  }

  redirectToHelp = () => window.open('https://help.oliv.com/searching-the-community/cancelling-your-membership', '_blank')

  get productAmount () {
    const { t } = this.props
    return (
      <div className='database-search-summary__wrapper'>
        <span>{t('plansPage.products.databaseSearch.title')}</span>
        <span className='database-search-summary__description'>
          <Trans i18nKey='plansPage.products.databaseSearch.summaryDescription'>
            View unlimited profiles and contact up to 50 profiles every month. Renewed every month automatically until you <a className='click-able-link' onClick={this.redirectToHelp}>cancel</a> your plan
          </Trans>
        </span>
      </div>
    )
  }

  renderTooltipContent = () => {
    const { t } = this.props
    return (
      <div className='database-search-order__tooltip-content'>
        <span>{`${t('plansPage.products.customized.tooltips.contacts.contactsHeader')}:`}</span>
        <ul>
          <li>{t('plansPage.products.customized.tooltips.contacts.cvDownload')}</li>
          <li>{t('plansPage.products.customized.tooltips.contacts.inviteCandidate')}</li>
          <li>{t('plansPage.products.customized.tooltips.contacts.revealContact')}</li>
        </ul>
      </div>
    )
  }

  get order () {
    const { t, price, currency } = this.props
    const tooltipEventType = isMobile() ? 'click' : 'mouseenter'
    const priceWithCurrency = getPriceWithCurrency(price, currency)

    return (
      <div className='database-search-order'>
        <div className='database-search-order__title'>{t('order.cvSearch')}</div>
        <div className='database-search-order__order-review'>
          <div className='database-search-order__order-product'>
            <div className='database-search-order__order-product-name'>
              {t('plansPage.products.databaseSearch.title')}
            </div>
            <div className='database-search-order__order-product-price'>
              <span className='database-search-order__order-product-price-label'>
                {t('order.cvStartingAt', { priceWithCurrency })}
              </span>
            </div>
          </div>
          <div className='database-search-order__select'>
            <div className='database-search-order__select-label'>
              <span className='database-search-order__select-label--title'>{t('plansPage.products.jobPost.total')}</span>
              <span className='database-search-order__select-label--amount'>50</span>
              {t('plansPage.products.databaseSearch.contacts')}
              <Tooltip className='database-search-order__tooltip' trigger={tooltipEventType} content={this.renderTooltipContent()} ownTheme='contacts-popper'>
                <i className='ion-md-information-circle' />
              </Tooltip>
            </div>
          </div>
        </div>
      </div>
    )
  }

  handleTransaction = async () => {
    await this.props.createProductTransaction()
    const { t, paymentResponseStatus, orderRef } = this.props
    const showError = () => NotificationService.error(t('errors.transactionCreateError'))
    const redirectToCheckout = () => this.props.history.push(`/pricing/candidates/checkout/${orderRef}`)

    return ifElse(
      pipe(equals(200), not),
      showError,
      redirectToCheckout
    )(paymentResponseStatus)
  }

  redirectToLogin = () => this.props.history.push('/employer/login?selfCheckout=jobPosts')

  onCheckout = () => ifElse(
    equals(true),
    this.handleTransaction,
    this.redirectToLogin
  )(this.props.isAuthenticated)

  render () {
    const { t, price, currency, orderRef } = this.props

    return (
      <div>
        <Helmet>
          <title>{cvSearchPricing.title}</title>
          <meta name='description' content={cvSearchPricing.description} />
          <meta property='og:title' content={cvSearchPricing.title} />
          <meta property='og:type' content='website' />
          <meta property='og:image' content={cvSearchPricing.image} />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:url' content={cvSearchPricing.url} />
          <meta property='og:description' content={cvSearchPricing.description} />
        </Helmet>
        <Order
          reviewUrl={StaticRoutes.creditsReview}
          checkoutUrl={`/pricing/candidates/checkout/${orderRef}`}
          disableCheckoutLink={orderRef === ''}
          order={this.order}
          productAmount={this.productAmount}
          price={getPriceWithCurrency(price, currency)}
          benefits={[
            t('plansPage.products.databaseSearch.benefits.screenedCommunity'),
            t('plansPage.products.databaseSearch.benefits.cvs'),
            t('plansPage.products.databaseSearch.benefits.filters'),
            t('plansPage.products.databaseSearch.benefits.sort')
          ]}
          benefitsTitle={t('order.databaseSearchBenefitsTitle')}
          onCheckout={this.onCheckout}
          checkoutButtonText={t('order.checkout')}
        />
      </div>
    )
  }
}

export default compose(withTranslations)(EmployerOrderCredits)



